@import url(https://fonts.googleapis.com/css2?family=Raleway&family=Ranchers&display=swap);
* {
  margin: 0;
  font-family: 'Raleway', sans-serif;
}

html {
  background-color: #b2ebf2;
  text-align: center;
  background-image: url('https://www.transparenttextures.com/patterns/connected.png');
  position: relative;
  min-height: 100%;
}

body {
  margin: 0 0 100px;
}

@media only screen and (max-width: 1536px) {
}

@media only screen and (max-width: 600px) {
}

/* Code without media query = Desktop Viewport */
nav {
  background-color: #ff5722;
  /* padding-bottom: 0.75rem; */
  text-align: center;
  margin-bottom: 2%;
  border-bottom: 2px solid #dd2c00;
}

nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  margin: 1.25rem 10rem;
  font-size: 1.35rem;
}

nav a {
  margin: 0;
  color: #ffff;
  text-decoration: none;
}

.home-link {
  font-family: 'Ranchers', cursive;
  font-size: 2.25rem;
  -webkit-text-stroke: 1px black;
}

.posts-link {
  font-weight: 600;
  text-decoration: underline;
}

.create-link {
  font-weight: 600;
  text-decoration: underline;
}

nav a:hover {
  color: #00bcd4;
}

.mobile-elements {
  display: none;
}

/* Laptops */
@media only screen and (max-width: 1536) {
}

/* Mobile Phones */
@media only screen and (max-width: 600px) {
  nav li {
    display: none;
  }
  nav {
    padding-bottom: 15%;
  }
  .hamburger-icon {
    display: inline;
    display: initial;
    position: absolute;
    left: 5%;
    top: 2%;
  }
  .home-link-mobile {
    font-family: 'Ranchers', cursive;
    -webkit-text-stroke: 1px black;
    display: inline;
    display: initial;
    position: absolute;
    font-size: 2.25rem;
    top: 0.75%;
    right: 23%;
  }

  .mobile-elements {
    display: inline;
    display: initial;
  }

  .mobile-menu {
    z-index: 1;
    width: 40%;
    height: 100%;
    background-color: #00bcd4;
    opacity: 97%;
    position: absolute;
    -webkit-animation: 0.75s ease-out 0s 1 slide-in-from-left;
            animation: 0.75s ease-out 0s 1 slide-in-from-left;
    -webkit-animation-name: slide-in-from-left;
    -webkit-animation-duration: 0.75s;
    -webkit-animation-iteration-count: 1;
    -webkit-animation-timing-function: linear;
    -webkit-animation-fill-mode: forwards;
  }

  .mobile-menu ul li {
    display: inline;
  }

  @-webkit-keyframes slide-in-from-left {
    0% {
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
      -ms-transform: translateX(-100%);
      -moz-transform: translateX(-100%);
      -o-transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
      -ms-transform: translateX(0);
      -moz-transform: translateX(0);
      -o-transform: translateX(0);
    }
  }

  @keyframes slide-in-from-left {
    0% {
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
      -ms-transform: translateX(-100%);
      -moz-transform: translateX(-100%);
      -o-transform: translateX(-100%);
    }
    100% {
      -webkit-transform: translateX(0);
      transform: translateX(0);
      -ms-transform: translateX(0);
      -moz-transform: translateX(0);
      -o-transform: translateX(0);
    }
  }

  nav a:hover {
    color: #ffff;
  }

  .menu-test-button {
    position: relative;
    right: 150px;
  }

  .posts-link-mobile {
    position: absolute;
    left: 10px;
    top: 65px;
    font-size: 2.5rem;
  }

  .create-link-mobile {
    position: absolute;
    left: 10px;
    top: 150px;
    font-size: 2.5rem;
  }

  .mobile-menu-exit-button {
    margin-top: 5%;
    margin-left: 65%;
    color: #ffff;
    -webkit-text-stroke: 1px black;
  }
}

.welcome-heading {
  text-align: left;
  text-align: initial;
  width: 20%;
  margin-left: 25%;
  margin-top: 14rem;
  font-size: 3rem;
  -webkit-animation: 1s ease-out 0s 1 move-in-from-left;
          animation: 1s ease-out 0s 1 move-in-from-left;
}

@-webkit-keyframes move-in-from-left {
  0% {
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

@keyframes move-in-from-left {
  0% {
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
  }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
  }
}

.welcome-text {
  float: right;
  width: 25%;
  display: block;
  font-size: 1.5rem;
  position: relative;
  bottom: 10rem;
  right: 25%;
  /* margin-right: 34.375rem; */
  text-align: left;
}

.welcome-text-section {
  color: #ff5722;
}

.Create {
  text-align: center;
  margin: 0 auto;
}

.Create h1 {
  margin-bottom: 4%;
  color: #00bcd4;
}

.post-inputs {
  display: block;
  margin: 5px auto;
  font-size: 1.15rem;
}

.post-button {
  padding: 10px;
  width: 4%;
  border-radius: 15px;
  border: none;
  background-color: #ff5722;
  color: #ffff;
}

.post-button:hover {
  background-color: #dd2c00;
  cursor: pointer;
}

.loading-spinner {
  position: absolute;
  right: 48.25%;
  -webkit-animation-name: spinning;
          animation-name: spinning;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  /* linear | ease | ease-in | ease-out | ease-in-out */
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes spinning {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spinning {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

textarea {
  border: 1px solid #dd2c00;
  border-radius: 30px;
  padding: 10px;
}

input[type='text'] {
  width: 245px;
  border: 1px solid #dd2c00;
  border-radius: 30px;
  box-shadow: 1px 1px 2px black;
  height: 20px;
  padding: 5px;
}

input[type='text']:focus,
textarea:focus {
  outline: 3px solid #ff5722;
}

.Posts h1 {
  margin-bottom: 4%;
  color: #00bcd4;
}

.post {
  margin: 2% auto;
  border: 3px solid #00bcd4;
  border-radius: 30px;
  width: 23%;
  padding: 2%;
  box-shadow: 2.5px 2.5px 10px black;
}

.post h2,
.post h3 {
  margin-bottom: 2%;
}

.post p {
  width: 60%;
  margin: 3% auto 2%;
}

.Admin table {
  text-align: center;
  margin: 2% auto;
  border: 2px solid black;
}

.Admin {
  background-color: white;
}

.Admin td {
  border: 1px solid black;
  padding: 1.5%;
}

.post-row .delete-button {
  color: red;
  position: relative;
  top: 5%;
}

footer {
  position: absolute;
  left: 46.45%;
  bottom: 1.5%;
}

@media only screen and (max-width: 1536px) {
  .welcome-text {
    bottom: 10rem;
    width: 29.1%;
    right: 17.5%;
  }

  .welcome-heading {
    width: 25%;
  }

  .post-button {
    width: 5.5%;
    margin-top: 7px;
  }
}

@media only screen and (max-width: 600px) {
  .welcome-heading {
    width: 75%;
    font-size: 2.25rem;
    text-align: center;
    margin: 3% auto;
    -webkit-animation: 1.5s ease-out 0s 1 drop_down;
            animation: 1.5s ease-out 0s 1 drop_down;
  }

  @-webkit-keyframes drop_down {
    0% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
    }
    100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }

  @keyframes drop_down {
    0% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
    }
    100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }

  .welcome-text {
    width: 95%;
    position: static;
    position: initial;
    margin-top: 5%;
    color: #00bcd4;
  }

  .Create {
    margin-top: 10%;
  }

  .post-button {
    width: 25%;
  }

  .post-button:hover {
    color: #ff5722;
  }

  .post {
    width: 75%;
  }

  .Admin {
    display: none;
  }

  footer {
    left: 34.5%;
  }
}

